@import '../helpers/_mixins.scss';

main {
    // Start Home Interface
    .home-banner {
        height: 650px;
        padding: 170px 100px;
        position: relative;
        &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: url('../images/header/svg-img.svg') no-repeat top right/contain;
            z-index: -1;
        }
        .home-info {
            position: relative;
            z-index: 2;
            h1 {
                font-weight: bold;
                text-transform: uppercase;
                letter-spacing: 1px;
                margin-bottom: 1.5rem;
            }
            .subtitle {
                font-family: merriweatherregular;
                font-size: 21px;
                color: #a5a5a5;
                line-height: 1.5;
                margin-bottom: 2rem;
            }
            .store-btn {
                a {
                    background: #fff;
                    color: #633991;
                    border: 1px solid #633991;
                    &:hover {
                        background-color: #633991;
                        color: #ffffff;
                        border: 1px solid #633991;
                    }
                }
                a.active {
                    background-color: #633991;
                    color: #ffffff;
                    border: 1px solid #633991;
                    &:hover {
                        background: #fff;
                        color: #633991;
                        border: 1px solid #633991;
                    }
                }
            }
        }
        .home-image { z-index: 9999; }
    }
    // End Home Interface
    
    // Start Features
    .features {
        padding: 140px 0 100px;
        .features-info { padding: 0 50px; }
    }
    // End Features

    // Start About
    .about {
        padding: 100px 0;
        background: #faf6fb;
        h2 {
            margin-bottom: 30px !important;
        }
        .about-head {
            margin-bottom: 70px;
            p {
                text-transform: capitalize;
                color: #888888;
                line-height: 1.9;
            }
        }
        .about-info { margin-bottom: 40px; }
    }
    // End About

    // Start Subscribe
    .subscribe {
        background-color: #faf6fb;
        .newsletter-info {
            padding: 100px 0;
            display: flex;
            flex-direction: column;
            max-width: 450px;
            margin: 0 auto;
            h2 {
                margin-bottom: 30px !important;
                &:after {
                    right: auto !important;
                }
            }
            p {
                margin-bottom: 4rem;
            }
            .newsletter {
                input {
                    background-color: #ffffff;
                    color: #a5a5a5 !important;
                    border: none;
                    border-radius: 4px;
                    font-size: 1.1rem;
                    height: calc(3.9rem + 1px);
                    padding: 1rem;
                }
                input::placeholder {
                    color: #a5a5a5;
                }
                span {
                    border-left: 1px solid #f1f2f3;
                    button {
                        background-color: #fff;
                        border: 0;
                        border-radius: 4px;
                        padding: 17px 30px;
                        color: #633991;
                        font-size: 1.25rem;
                    }
                }
            }
        }
        .video-container {
            background: url("../images/subscribe/hero-img.jpg");
            background-size: cover;
            width: 100%;
            height: 100%;
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            color: #ffffff;
            z-index: 1;
            padding: 200px 50px;
            &:after {
                @include overlay;
                content: "";
                background: rgba(155, 27, 229, 0.9);
            }
            .video-content {
                z-index: 2;
                a {
                    background-image: linear-gradient(122deg, #e54595 0%, #fd378e 100%);
                    color: #ffffff;
                    width: 80px;
                    height: 80px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-radius: 100%;
                    margin: auto;
                    margin-bottom: 1.5rem;
                    position: relative;
                    text-decoration: none;
                    i {
                        font-size: 40px;
                        margin-left: 5px;
                    }
                }
                .circle {
                    border-radius: 50%;
                    background-color: transparent;
                    border: 1px solid #eee;
                    width: 150px;
                    height: 150px;
                    position: absolute;
                    opacity: 0;
                    z-index: -1;
                    animation: scaleIn 1s infinite linear;
                }
            }
        }
    }
    // End Subscribe

    // Start Pricing
    .pricing {
        padding: 100px 0;
        .pricing-card {
            border: 1px solid #f1f2f3;
            width: 100%;
            padding: 2rem;
            border-radius: 4px;
            margin-bottom: 30px;
            transition: all 0.5s;
            .pricing-header {
                small {
                    color: #e54595;
                    font-weight: 500;
                }
                span {
                    display: block;
                    color: #633991;
                    font-size: 2.8rem;
                    font-weight: 300;
                }
                ul {
                    padding: 2rem 0;
                    li {
                        border: none;
                        color: #868686;
                        font-size: 17px;
                    }
                }
            }
            .pricing-footer {
                .card-btn {
                    font-family: "Montserrat", sans-serif;
                    display: inline-block;
                    text-transform: uppercase;
                    padding: 1rem 2rem;
                    line-height: 1;
                    margin-top: 1rem;
                    font-size: 0.75rem;
                    font-weight: 600;
                    min-width: 80px;
                    border-color: transparent;
                    letter-spacing: 1px;
                    background-color: #633991;
                    background-image: linear-gradient(122deg, #e54595 0%, #fd378e 100%);
                    background-clip: border-box, padding-box;
                    color: #ffffff;
                    border: 2px double transparent;
                    border-radius: 5px;
                    text-decoration: none;
                }
            }
        }
        .popular {
            box-shadow: 0px 10px 30px rgba(130, 82, 250, 0.2);
            border-color: #e54595;
        }
    }
    // End Pricing

    // Start Testimonials
    .testimonials {
        padding: 100px 0;
        text-align: center;
        background: #faf6fb;
        .owl-carousel {
            .item {
                .author-image {
                    display: block;
                    margin-bottom: 30px;
                    img {
                        width: 100px;
                        height: 100px;
                        border-radius: 50%;
                        display: inline-block;
                    }
                }
                .info {
                    position: relative;
                    .author-text {
                        width: 70%;
                        margin: auto;
                        p {

                        }
                    }
                    .author-name {
                        margin-top: 30px;
                        h6 {
                            font-weight: bold;
                            small {
                                font-size: 0.75rem;
                                color: #a5a5a5;
                                &:before {
                                    content: '/';
                                    display: inline-block;
                                    margin-right: 5px;
                                    margin-left: 5px;
                                }
                            }
                        }
                        ul {
                            li {
                                margin-right: 2px;
                                font-size: 0.75rem;
                                i {
                                    color: #fac50f;
                                }
                            }
                        }
                    }                     
                    &:before {
                        content: '\f10d';
                        display: inline-block;
                        position: absolute;
                        left: 7%;
                        top: -3px;
                        bottom: 0;
                        width: 30px;
                        height: 50px;
                        line-height: 1;
                        font-family: "FontAwesome";
                        color: #633991;
                        font-size: 2rem;
                    }
                    &:after {
                        content: '\f10e';
                        display: inline-block;
                        position: absolute;
                        right: 7%;
                        bottom: 0px;
                        width: 30px;
                        height: 50px;
                        line-height: 1;
                        font-family: "FontAwesome";
                        color: #633991;
                        font-size: 2rem;
                    }
                }
            }
            .owl-stage {
                transition: all 1.2s ease 0s !important;
            }
            .owl-dots {
                margin-top: 30px;
                text-align: center;
                button {
                    background-color: unset;
                    border: 1px solid #633991;
                    width: 25px;
                    height: 6px;
                    display: inline-block;
                    margin-right: 10px;
                    border-radius: 4px;
                    span {

                    }
                    &:focus {
                        outline: 0;
                        text-decoration: none;
                    }
                }
                button.active {
                    background: #633991;
                }
            }
        }
    }
    // End Testimonials

    // Start Download
    .download {
        position: relative;
        padding: 100px 0;
        background: url("../images/download/hero-img2.jpg") no-repeat center center fixed;
        color: #ffffff;
        &:after {
            background-image: linear-gradient(90deg, #ff0084 0%, #9b1be5 100%);
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            content: "";
            opacity: 0.9;
        }
        .container {
            z-index: 2;
            position: relative;
            .download-info {
                h2 {
                    font-family: "Poppins", sans-serif;
                    font-weight: bold;
                    letter-spacing: 1px;
                    margin-bottom: 2rem;
                }
                p {
                    line-height: 1.9;
                    margin-bottom: 2.5rem;
                }
                .download-btn {
                    a {
                        background: transparent;
                        color: #ffffff;
                        border: 1px solid #ffffff;
                        &:hover {
                            color: #633991;
                            background-color: #ffffff;
                            border: 1px solid #ffffff;
                        }
                    }
                    a.active {
                        color: #633991;
                        background-color: #ffffff;
                        border: 1px solid #ffffff;
                        &:hover {
                            background: transparent;
                            color: #ffffff;
                            border: 1px solid #ffffff;
                        }
                    }
                }
            }
            .download-image {
                position: absolute;
                top: 0;
            }
        }
    }
    // End Download

    // Start Contact
    .contact-us {
        padding: 80px 0 50px;;
        address {
            .location {
                p {
                    color: #a7a7a7;
                    display: block;
                    margin-bottom: 1rem;
                }
            }
            .contact {
                display: inline-flex;
                p {
                    &:first-child {
                        margin-right: 20px
                    }
                    a, span {
                        color: #e54595;
                        text-decoration: none;
                    }
                }
            }
            p {
                margin-bottom: 0;
                display: inline-flex;
                align-items: center;
                i {
                    font-size: 22px;
                    margin-right: 15px;
                    color: #a7a7a7;
                }
                .fa-envelope-o {
                    font-size: 19px;
                    position: relative;
                    left: -3px;
                }
            }
        }
        ul.social-icons {
            text-align: end;
            margin: 0;
            li {
                margin-right: .5rem;
                a {
                    width: 40px;
                    height: 40px;
                    color: #e54595;
                    background: #ffffff;
                    border-radius: 5px;
                    box-shadow: 0px 10px 30px rgba(130, 82, 250, 0.2);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    text-decoration: none;
                }
            }

        }
    }
    // End Contact

    // Start Global
    .features, .about, .slider, .subscribe, .pricing, .testimonials {
        h2 {
            font-family: "Poppins", serif;
            letter-spacing: 1px;
            margin-bottom: 70px;
            text-transform: uppercase;
            position: relative;
            padding-bottom: 1rem;
            &:after {
                @include line-gradient;
            }
        }
        p {
            color: #a5a5a5;
            line-height: 1.9;
            font-family: merriweatherlight;
            font-size: 15px;
        }
    }
    .features-info, .about-info {
        i {
            @include icon-gradient;
        }
        h6 {
            margin-bottom: 1rem;
            font-weight: 600;
            font-size: 1.1rem;
            font-family: "Poppins", sans-serif;
        }
    }
    // End Global
}

// Animations
@keyframes scaleIn {
    from {
      transform: scale(.5, .5);
      opacity: .5;
    }
    to {
      transform: scale(1, 1);
      opacity: 0;
    }
  }
